import "./App.css";
import { lazy, Suspense, useState } from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { Toaster } from "react-hot-toast";
import { useOutletContext } from "react-router-dom";

import HomePage from "./Component/pages/HomePage";
import UploadPhoto from "./Component/pages/UploadImageUrl";

const Products = lazy(() => import("./Component/pages/Products"));
const ProductDetail = lazy(() => import("./Component/pages/productDetailPage"));
const BlogsPage = lazy(() => import("./Component/pages/BlogPage"));
const BlogDetailPage = lazy(() => import("./Component/pages/BlogDetailPage"));
const AboutUsPage = lazy(() => import("./Component/pages/AboutUs"));
const ContactPage = lazy(() => import("./Component/pages/ContactPage"));
const AuthPage = lazy(() => import("./Component/pages/AuthPage"));
const ShoppingBasketPage = lazy(() =>
  import("./Component/pages/ShoppingBasketPage")
);
const CheckoutPage = lazy(() => import("./Component/pages/Checkout"));
const Dashboard = lazy(() => import("./Component/pages/Dashboard"));
const NotFoundPage = lazy(() => import("./Component/pages/NotFound"));

// Dashboard - Admin
const Overview = lazy(() =>
  import("./Component/ComponentChilds/Dashboard/admin/Overview")
);
const ProductManagement = lazy(() =>
  import(
    "./Component/ComponentChilds/Dashboard/admin/Products/ProductManagement"
  )
);
const UsersPage = lazy(() =>
  import("./Component/ComponentChilds/Dashboard/admin/Users/UsersPage")
);
const BlogManagement = lazy(() =>
  import("./Component/ComponentChilds/Dashboard/admin/Blogs/BlogManagment")
);
const AddBlog = lazy(() =>
  import("./Component/ComponentChilds/Dashboard/admin/Blogs/AddBlog")
);
const EditBlog = lazy(() =>
  import("./Component/ComponentChilds/Dashboard/admin/Blogs/EditBlog/EditBlog")
);
const OrdersPage = lazy(() =>
  import("./Component/ComponentChilds/Dashboard/admin/Orders/AdminOrdersPage")
);
const ManageTickets = lazy(() =>
  import("./Component/ComponentChilds/Dashboard/admin/Tickets/AdminTicketsPage")
);
const ViewTicket = lazy(() =>
  import("./Component/ComponentChilds/Dashboard/admin/Tickets/ViewTicket")
);

// Dashboard - User
const UserOrdersPage = lazy(() =>
  import("./Component/ComponentChilds/Dashboard/user/UserOrdersPage")
);
const UserProfilePage = lazy(() =>
  import("./Component/ComponentChilds/Dashboard/user/UserProfilePage")
);
const TicketSystem = lazy(() =>
  import("./Component/ComponentChilds/Dashboard/user/tickets/TicketSystem")
);

const ResetPassowrd = lazy(() =>
  import("./Component/ComponentChilds/Login/ResetPassowrd")
);

const TicketDetailView = lazy(() =>
  import("./Component/ComponentChilds/Dashboard/user/tickets/TicketDetailView")
);

// const UploadPhoto = lazy(() => import("./Component/pages/UploadImageUrl"));
// import ResetPassword from "./Component/ComponentChilds/Login/ResetPassowrd";

export default function App() {
  const basketItemLength = JSON.parse(
    localStorage.getItem("shoppingBasket")
  )?.length;
  const [basketItemCount, setBasketItemCount] = useState(basketItemLength || 0);

  return (
    <Router>
      <Toaster />
      <Suspense
        fallback={<div className="text-center mt-10">درحال بارگذاری...</div>}
      >
        <Routes>
          {/* Public Pages */}
          <Route
            path="/"
            element={
              <HomePage
                basketItemCount={basketItemCount}
                onSetBasketItemCount={setBasketItemCount}
              />
            }
          />
          <Route
            path="/products"
            element={
              <Products
                basketItemCount={basketItemCount}
                onSetBasketItemCount={setBasketItemCount}
              />
            }
          />
          <Route
            path="/products/:productName"
            element={
              <ProductDetail
                basketItemCount={basketItemCount}
                onSetBasketItemCount={setBasketItemCount}
              />
            }
          />
          <Route
            path="/blogs"
            element={<BlogsPage basketItemCount={basketItemCount} />}
          />
          <Route
            path="/blogs/:blogName"
            element={<BlogDetailPage basketItemCount={basketItemCount} />}
          />
          <Route
            path="/about"
            element={<AboutUsPage basketItemCount={basketItemCount} />}
          />
          <Route
            path="/contact"
            element={<ContactPage basketItemCount={basketItemCount} />}
          />
          <Route path="/auth" element={<AuthPage />} />
          <Route path="/auth/restpass" element={<ResetPassowrd />} />
          <Route
            path="/cart"
            element={<ShoppingBasketPage basketItemCount={basketItemCount} />}
          />
          <Route
            path="/checkout"
            element={
              <CheckoutPage
                basketItemCount={basketItemCount}
                onSetBasketItemCount={setBasketItemCount}
              />
            }
          />

          <Route path="/upload" element={<UploadPhoto />} />

          {/* Dashboard */}
          <Route path="/dashboard" element={<Dashboard />}>
            <Route index element={<RoleBaseFirstDashboardPage />} />
            <Route path="products" element={<ProductManagement />} />
            <Route path="users" element={<UsersPage />} />
            <Route path="blogs" element={<BlogManagement />} />
            <Route path="blogs/add" element={<AddBlog />} />
            <Route path="blogs/edit/:blogID" element={<EditBlog />} />
            <Route path="orders" element={<RoleBasedOrders />} />
            <Route path="tickets" element={<RoleBasedTickets />} />
            <Route
              path="tickets/:ticketId"
              element={<RoleBasedTicketDetails />}
            />
            <Route path="profile" element={<UserProfilePage />} />
          </Route>

          {/* 404 Not Found */}
          <Route path="*" element={<NotFoundPage />} />
        </Routes>
      </Suspense>
    </Router>
  );
}

// Dashboard Role-Based Logic
function RoleBaseFirstDashboardPage() {
  const { role } = useOutletContext();
  return role === "user" ? <UserOrdersPage /> : <Overview />;
}

function RoleBasedOrders() {
  const { role } = useOutletContext();
  return role === "user" ? <UserOrdersPage /> : <OrdersPage />;
}

function RoleBasedTickets() {
  const { role } = useOutletContext();
  return role === "user" ? <TicketSystem /> : <ManageTickets />;
}

function RoleBasedTicketDetails() {
  const { role } = useOutletContext();
  return role === "user" ? <TicketDetailView /> : <ViewTicket />;
}
