import React, { useState } from "react";

const UploadPhoto = () => {
  const [file, setFile] = useState(null);
  const [previewUrl, setPreviewUrl] = useState(null);
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState(null);
  const [error, setError] = useState("");

  // const API_URL =
  //   window.location.hostname === "localhost"
  //     ? "http://127.0.0.1:8000/api/upload-images/"
  //     : "https://allmaxsport.com/api/upload-images/";
  const API_URL = "https://allmaxsport.com/api/upload-images/";

  const handleFileChange = (e) => {
    const selected = e.target.files[0];
    if (selected) {
      setFile(selected);
      setPreviewUrl(URL.createObjectURL(selected));
      setResult(null);
      setError("");
    }
  };

  const handleUpload = async () => {
    if (!file) {
      setError("Please select an image first.");
      return;
    }

    setLoading(true);
    setError("");
    setResult(null);

    const formData = new FormData();
    formData.append("image", file);

    try {
      const res = await fetch(API_URL, {
        method: "POST",
        body: formData,
      });

      if (!res.ok) throw new Error("Server error");

      const data = await res.json();
      setResult(data.image_urls[0]);
    } catch (err) {
      setError("Upload failed. Please check your connection or try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-900 text-white flex items-center justify-center px-4 py-10">
      <div className="w-full max-w-lg bg-gray-800/60 backdrop-blur-md rounded-2xl p-6 shadow-2xl border border-gray-700">
        <h1 className="text-2xl font-bold text-center mb-6 text-blue-400">
          Upload a Photo
        </h1>

        <label className="flex flex-col items-center justify-center border-2 border-dashed border-gray-600 hover:border-blue-500 transition rounded-xl h-52 cursor-pointer">
          <input
            type="file"
            accept="image/*"
            onChange={handleFileChange}
            className="hidden"
          />
          {previewUrl ? (
            <img
              src={previewUrl}
              alt="Preview"
              className="h-full object-contain rounded-md"
            />
          ) : (
            <span className="text-gray-400 text-sm">
              Click or drag an image here
            </span>
          )}
        </label>

        {error && (
          <p className="text-red-500 text-sm mt-3 text-center">{error}</p>
        )}

        <button
          onClick={handleUpload}
          disabled={loading}
          className="w-full mt-5 bg-blue-600 hover:bg-blue-700 text-white font-medium py-2.5 rounded-xl transition"
        >
          {loading ? "Uploading..." : "Upload"}
        </button>

        {result && (
          <div className="mt-6 bg-gray-700 p-4 rounded-xl text-sm text-gray-200 overflow-auto max-h-64">
            <h2 className="font-semibold text-blue-400 mb-2">API Response:</h2>
            <pre className="whitespace-pre-wrap break-words">
              {JSON.stringify(result, null, 2)}
            </pre>
          </div>
        )}
      </div>
    </div>
  );
};

export default UploadPhoto;
