import { useEffect, useRef, useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { X, Search } from "lucide-react";
import { Link } from "react-router-dom";

const suggestionsDefault = ["تردمیل", "الپتیکال", "دوچرخه ثابت"];

export default function SearchModal({ isOpen, onClose, isMobile = false }) {
  const inputRef = useRef();
  const [query, setQuery] = useState("");
  const [results, setResults] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    if (isOpen) {
      inputRef.current?.focus();
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "auto";
      setQuery("");
      setResults([]);
    }

    const handleEsc = (e) => {
      if (e.key === "Escape") onClose();
    };
    document.addEventListener("keydown", handleEsc);
    return () => document.removeEventListener("keydown", handleEsc);
  }, [isOpen, onClose]);

  useEffect(() => {
    if (!query) {
      setResults([]);
      return;
    }

    const controller = new AbortController();
    const signal = controller.signal;

    async function fetchResults() {
      try {
        setLoading(true);
        const response = await fetch(
          `http://127.0.0.1:8000/products/?name=${encodeURIComponent(query)}`,
          { signal }
        );
        if (!response.ok) throw new Error("Failed to fetch");

        const data = await response.json();
        setResults(data);
      } catch (e) {
      } finally {
        setLoading(false);
      }
    }

    fetchResults();

    return () => controller.abort();
  }, [query]);

  const slugify = (text) => {
    return text
      .toString()
      .normalize("NFD")
      .replace(/[\u064B-\u0652]/g, "")
      .replace(/\s+/g, "-")
      .replace(/[^\w\-آ-ی]/g, "")
      .replace(/\-\-+/g, "-")
      .replace(/^-+/, "")
      .replace(/-+$/, "");
  };

  return (
    <AnimatePresence>
      {isOpen && (
        <>
          {/* Overlay */}
          <motion.div
            className="fixed inset-0 bg-black/40 z-40 backdrop-blur-sm"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={onClose}
          />

          {/* Drawer */}
          <motion.div
            className={`fixed top-0 right-0 z-50 bg-white shadow-xl p-6 overflow-y-auto ${
              isMobile ? "w-full h-full" : "h-full w-full max-w-xl"
            }`}
            initial={{ x: "100%" }}
            animate={{ x: 0 }}
            exit={{ x: "100%" }}
            transition={{ type: "spring", stiffness: 300, damping: 30 }}
            onClick={(e) => e.stopPropagation()}
          >
            {/* Top Close */}
            <div className="w-full flex justify-end items-center mb-4">
              <button
                className="text-gray-500 hover:text-black"
                onClick={onClose}
                aria-label="بستن جستجو"
              >
                <X />
              </button>
            </div>

            {/* Input */}
            <div className="relative">
              <Search className="absolute right-4 top-1/2 -translate-y-1/2 text-gray-400" />
              <input
                ref={inputRef}
                type="text"
                placeholder="جستجو کنید..."
                value={query}
                onChange={(e) => setQuery(e.target.value)}
                className="w-full pr-10 pl-4 py-3 border border-gray-300 rounded-lg text-gray-800 text-lg focus:outline-none focus:ring focus:ring-blue-200"
              />
              {query && (
                <button
                  className="absolute left-4 top-1/2 -translate-y-1/2 text-gray-500 hover:text-black"
                  onClick={() => setQuery("")}
                  aria-label="پاک کردن جستجو"
                >
                  <X />
                </button>
              )}
            </div>

            {/* Suggestions */}
            {!query && (
              <div className="mt-6">
                <p className="text-sm text-gray-500 mb-2">پیشنهادات:</p>
                <div className="flex flex-wrap gap-2">
                  {suggestionsDefault.map((s, idx) => (
                    <button
                      key={idx}
                      onClick={() => setQuery(s)}
                      className="px-3 py-1 bg-gray-100 hover:bg-gray-200 rounded-full text-sm"
                    >
                      {s}
                    </button>
                  ))}
                </div>
              </div>
            )}

            {/* Loading Skeleton */}
            {loading && (
              <div className="mt-6 space-y-4">
                {[1, 2, 3, 4].map((i) => (
                  <div
                    key={i}
                    className="flex items-center gap-4 animate-pulse bg-gray-100 p-3 rounded-lg"
                  >
                    <div className="w-16 h-16 bg-gray-300 rounded" />
                    <div className="flex-1 space-y-2">
                      <div className="h-4 bg-gray-300 rounded w-3/4" />
                      <div className="h-4 bg-gray-300 rounded w-1/2" />
                    </div>
                  </div>
                ))}
              </div>
            )}

            {/* Results */}
            {!loading && query && results.length > 0 && (
              <motion.div
                className="mt-6 space-y-2 max-h-96 overflow-y-auto"
                initial="hidden"
                animate="visible"
                variants={{
                  hidden: {},
                  visible: {
                    transition: {
                      staggerChildren: 0.08,
                    },
                  },
                }}
              >
                {results.map((item, idx) => {
                  const regex = new RegExp(`(${query})`, "gi");
                  const parts = item.name.split(regex);
                  return (
                    <motion.div
                      key={item.id || idx}
                      variants={{
                        hidden: { opacity: 0, x: 20 },
                        visible: { opacity: 1, x: 0 },
                      }}
                    >
                      <Link
                        to={`/products/${slugify(item.name)}`}
                        className="flex items-center gap-4 p-3 rounded-lg bg-gray-100 hover:bg-gray-200 cursor-pointer transition"
                        onClick={onClose}
                      >
                        <img
                          src={item.images[0]}
                          alt={item.name}
                          className="w-16 h-16 object-cover rounded"
                        />
                        <div className="flex flex-col">
                          <div className="text-base font-medium text-gray-800">
                            {parts.map((part, i) =>
                              regex.test(part) ? (
                                <span
                                  key={i}
                                  className="text-blue-500 font-bold"
                                >
                                  {part}
                                </span>
                              ) : (
                                <span key={i}>{part}</span>
                              )
                            )}
                          </div>
                          <div className="text-sm text-gray-500 mt-1">
                            {Number(item.price)?.toLocaleString("fa-IR")} تومان
                          </div>
                        </div>
                      </Link>
                    </motion.div>
                  );
                })}
              </motion.div>
            )}

            {/* No results */}
            {!loading && query && results.length === 0 && (
              <div className="mt-6 text-center text-gray-400 text-sm">
                نتیجه‌ای یافت نشد.
              </div>
            )}
          </motion.div>
        </>
      )}
    </AnimatePresence>
  );
}
