import { useState } from "react";
import firstHeroImage from "../../../assets/images/1.jpg";
import secondHeroImage from "../../../assets/images/2.jpg";
import thrdHeroImage from "../../../assets/images/3.jpg";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper/modules";
import "swiper/css";
import "swiper/css/navigation";

import { ArrowLeft, ArrowRight } from "lucide-react";
import { Link } from "react-router-dom";

// row Hero Section

const slides = [
  {
    id: 1,
    title: "باشگاهت رو با بهترین تجهیزات بدنسازی مجهز کن",
    description:
      "واردات مستقیم و فروش تجهیزات باشگاهی حرفه‌ای با استانداردهای جهانی + تخفیف ویژه با خرید عمده و شرایط خرید نقد و اقساط. بهترین فرصت برای تجهیز باشگاهت همین حالاست!",
    image:
      "https://allmaxsport.com/Media/images/craig-lovelidge-ndr4vtteVGw-unsplash-ezgif.com-webp-to-avif-converter.avif",
    cta: "مشاهده محصولات و دریافت قیمت",
    slug: "products",
  },
  {
    id: 2,
    title: "واردات تجهیزات باشگاهی حرفه‌ای + مشاوره رایگان",
    description:
      "تجهیز باشگاهت با جدیدترین دستگاه‌های بدنسازی روز دنیا. کیفیت عالی، دوام بالا و خدمات پس از فروش مطمئن + شرایط خرید نقد و اقساط برای باشگاه‌داران.",
    image:
      "https://allmaxsport.com/Media/images/andrew-kayani-kCbCaLaiLLo-unsplash-ezgif.com-webp-to-avif-converter.avif",
    cta: "دریافت مشاوره رایگان و ثبت سفارش",
    slug: "contact",
  },
  {
    id: 3,
    title: "فروش تجهیزات باشگاهی مدرن با شرایط ویژه",
    description:
      "از مشاوره تا نصب تجهیزات باشگاهی، همراهت هستیم. تخفیف ویژه برای خرید عمده + واردات مستقیم دستگاه‌های بدنسازی اورجینال. الان بهترین زمان برای ارتقاء باشگاهته.",
    image:
      "https://allmaxsport.com/Media/images/ambitious-studio-rick-barrett-Ksl3D9tlfWs-unsplash-ezgif.com-webp-to-avif-converter.avif",
    cta: "تماس بگیرید و تخفیف بگیرید",
    slug: "contact",
  },
];

const HeroSection = () => {
  return (
    <section className="relative w-full h-[91.5vh] overflow-hidden">
      <Swiper
        modules={[Navigation]}
        navigation={{
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        }}
        loop={true}
        className="h-full"
      >
        {slides.map((slide) => (
          <SwiperSlide key={slide?.id}>
            <div className="relative h-full w-full">
              <img
                src={slide.image}
                sizes="(max-width: 768px) 100vw, 90vw"
                alt={slide.title}
                className="w-full h-full object-cover"
                loading="eager"
                fetchPriority="high"
              />

              <div className="absolute inset-0 bg-black/50 flex flex-col justify-center items-start p-10 text-white max-w-xl">
                <h1 className="text-4xl md:text-6xl font-bold mb-4">
                  {slide?.title}
                </h1>
                <p className="text-lg mb-6">{slide?.description}</p>
                <Link
                  to={`/${slide?.slug}`}
                  className="bg-white text-black px-6 py-3 rounded-xl font-semibold hover:bg-gray-200 transition"
                >
                  {slide?.cta}
                </Link>
              </div>
            </div>
          </SwiperSlide>
        ))}

        {/* Navigation Arrows */}
        <div className="swiper-button-prev absolute top-1/2 left-4 z-10 text-white">
          <ArrowLeft size={36} />
        </div>
        <div className="swiper-button-next absolute top-1/2 right-4 z-10 text-white">
          <ArrowRight size={36} />
        </div>
      </Swiper>
    </section>
  );
};

export default HeroSection;
