import { useState, useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import { Menu, X, ShoppingCart, UserRound, Search } from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import SearchModal from "./SearchModal";

function getCookie(name) {
  const cookies = document.cookie.split("; ");
  for (let cookie of cookies) {
    const [cookieName, cookieValue] = cookie.split("=");
    if (cookieName === name) return decodeURIComponent(cookieValue);
  }
  return null;
}

const menuItems = [
  { farsi: "خانه", path: "/" },
  { farsi: "محصولات", path: "/products" },
  { farsi: "مقالات", path: "/blogs" },
  { farsi: "درباره‌ی ما", path: "/about" },
  { farsi: "تماس با ما", path: "/contact" },
];

export default function Header({ basketItemCount }) {
  const [isLogin, setIsLogin] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);
  const [searchModalOpen, setSearchModalOpen] = useState(false);
  const location = useLocation();
  const userID = getCookie("userId");

  useEffect(() => {
    if (userID) setIsLogin(true);
  }, [userID]);

  useEffect(() => {
    document.body.style.overflow = menuOpen ? "hidden" : "auto";
    return () => (document.body.style.overflow = "auto");
  }, [menuOpen]);

  const isActive = (path) =>
    location.pathname === path ||
    (path !== "/" && location.pathname.startsWith(path));

  return (
    <header dir="rtl" className="sticky top-0 z-50 bg-white shadow">
      <nav className="flex items-center justify-between px-6 py-4 flex-wrap gap-y-4 text-gray-700">
        <Link to="/" aria-label="برگشت به خانه" className="font-bold text-lg">
          آلمکس اسپورت
        </Link>

        {/* Desktop Search */}
        <div className="hidden lg:block max-w-md flex-grow mx-4">
          <button
            onClick={() => setSearchModalOpen(true)}
            className="w-full flex items-center justify-start px-4 py-2 rounded-full border border-gray-300 text-gray-500 hover:text-black focus:ring focus:ring-blue-100 focus:outline-none"
          >
            <Search className="text-gray-400" />
            <span className="mr-2">جستجو...</span>
          </button>
        </div>

        {/* Desktop Nav */}
        <ul className="hidden lg:flex gap-6 text-sm font-medium">
          {menuItems.map(({ farsi, path }, i) => (
            <li key={i}>
              <Link
                to={path}
                className={`relative transition-all duration-300 ${
                  isActive(path) ? "text-black" : "text-gray-500"
                } before:content-[''] before:absolute before:bottom-0 before:right-0 before:h-[2px] before:bg-blue-400 
                  before:transition-all before:duration-300 before:ease-in-out
                  ${
                    isActive(path)
                      ? "before:w-full"
                      : "before:w-0 hover:before:w-full hover:text-black"
                  }`}
              >
                {farsi}
              </Link>
            </li>
          ))}
        </ul>

        {/* Icons + Menu */}
        <div className="flex items-center gap-4">
          <Link to={"/cart"} aria-label="سبد خرید">
            <div className="relative cursor-pointer">
              <ShoppingCart className="hover:text-black" />
              {basketItemCount > 0 && (
                <span className="absolute -top-2 -left-2 bg-red-500 text-white text-xs w-5 h-5 rounded-full flex items-center justify-center font-bold shadow">
                  {basketItemCount}
                </span>
              )}
            </div>
          </Link>

          {isLogin ? (
            <Link
              to="/dashboard"
              className="group relative border rounded-full p-1 hover:bg-blue-600 transition"
              aria-label="ورود به داشبورد"
            >
              <UserRound className="group-hover:text-white" />
            </Link>
          ) : (
            <Link
              to="/auth"
              className="relative group border px-3 py-1 rounded-lg text-sm overflow-hidden"
            >
              <span className="relative z-10 group-hover:text-white">
                ثبت نام / ورود
              </span>
              <span className="absolute inset-0 w-0 bg-blue-600 transition-all duration-300 group-hover:w-full"></span>
            </Link>
          )}

          <button
            onClick={() => setMenuOpen(true)}
            className="lg:hidden"
            aria-label="باز کردن منو"
          >
            <Menu />
          </button>
        </div>
      </nav>

      {/* 🔍 Mobile & Desktop Search Modal */}
      <SearchModal
        isOpen={searchModalOpen}
        onClose={() => setSearchModalOpen(false)}
        isMobile={window.innerWidth < 1024}
      />

      {/* 📱 Mobile Menu Drawer */}
      <AnimatePresence>
        {menuOpen && (
          <>
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 0.5 }}
              exit={{ opacity: 0 }}
              onClick={() => setMenuOpen(false)}
              className="fixed inset-0 bg-black z-40"
            />
            <motion.aside
              initial={{ x: "100%" }}
              animate={{ x: 0 }}
              exit={{ x: "100%" }}
              transition={{ duration: 0.35, ease: "easeInOut" }}
              className="fixed top-0 right-0 w-[80%] sm:w-[60%] h-full bg-white z-50 p-6 flex flex-col"
            >
              <div className="flex justify-between items-center mb-4">
                <p className="font-bold text-lg">منو</p>
                <button
                  onClick={() => setMenuOpen(false)}
                  aria-label="بستن منو"
                >
                  <X />
                </button>
              </div>

              <ul className="flex flex-col gap-4 text-sm font-medium">
                {menuItems.map(({ farsi, path }, idx) => (
                  <li key={idx}>
                    <Link
                      to={path}
                      className="block py-2 border-b hover:text-blue-600"
                      onClick={() => setMenuOpen(false)}
                    >
                      {farsi}
                    </Link>
                  </li>
                ))}
              </ul>

              {/* 📱 Mobile Search Trigger */}
              <div className="mt-6">
                <button
                  onClick={() => {
                    setMenuOpen(false);
                    setSearchModalOpen(true);
                  }}
                  className="w-full flex items-center justify-start px-4 py-2 rounded-full border border-gray-300 text-gray-500 hover:text-black focus:ring focus:ring-blue-100 focus:outline-none"
                >
                  <Search className="text-gray-400" />
                  <span className="mr-2">جستجو...</span>
                </button>
              </div>
            </motion.aside>
          </>
        )}
      </AnimatePresence>
    </header>
  );
}
